@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@100;300;400;500;600;700&display=swap);
.PageContainer {
    display: flex;

    width: calc(100% - 2 * 24px);
    height: calc(100% - 2 * 24px);

    padding: 24px;
}
.Button {
    position: relative;
    font-size: 12px;
    line-height: 16px;
    font-weight: 600;

    padding: 16px 28px;
    border-radius: 16px;
    background-color: var(--active-button-background-color);
    border: none;
}

.Button::before {
    content: '';
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;

    background: rgba(149, 117, 205, 0.56);
    filter: blur(24px);
    z-index: -1;
}

.Button-text {
    color: var(--active-button-color);
    font-weight: 700;
}

.Button:hover {
    box-shadow: -4px -4px 12px rgba(253, 255, 255, 0.8), 4px 4px 12px rgba(187, 195, 206, 0.6);
}

.Button:active {
    box-shadow: inset 0 0 12px;
}

.Button.disabled {
    background-color: var(--disabled-button-background-color);
}

.Button.disabled:hover,
.Button.disabled:active {
    box-shadow: none;
}

.Button.disabled .Button-text {
    color: var(--disabled-button-color);
}
.Card {
    width: -webkit-max-content;
    width: max-content;
    height: -webkit-max-content;
    height: max-content;

    padding: 24px;

    border-radius: 16px;
    background: linear-gradient(0deg, #E6EEF8, #E6EEF8), #CFD8DC;
    box-shadow: -16px -16px 40px var(--card-shadow-color-1), 16px 16px 40px var(--card-shadow-color-2);
}

.Card-full {
    width: 100%;
}

.Card-feat {
    width: -webkit-max-content;
    width: max-content;
}
.LoginPage {
    display: flex;

    align-items: center;
    justify-content: center;
}

.LoginPage__card {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.LoginPage__card-title {
    font-size: 16px;
    line-height: 22px;
    font-weight: 700;
    margin: 0;
}

.LoginPage__card-input {
    margin-top: 24px;
}

.LoginPage__card-error {
    visibility: hidden;
    font-size: 10px;
    margin: 8px 0;

    height: 14px;
    color: var(--error-text-color);
}

.LoginPage__card-link {
    margin-top: 24px;
    
    font-size: 12px;
    line-height: 16px;
}

.LoginPage__card-link > a {
    text-decoration: none;
}

.LoginPage__card-link + .LoginPage__card-link {
    margin-top: 8px;
}

.LoginPage__card-error.visible {
    visibility: visible;
}
.TextInput {
    position: relative;
    display: flex;

    min-width: 252px;

    padding: 16px;
    border: 1px solid transparent;
    background: linear-gradient(0deg, #E6EEF8, #E6EEF8), #CFD8DC;
    box-shadow: inset -4px -4px 12px rgba(253, 255, 255, 0.8), inset 4px 4px 12px rgba(187, 195, 206, 0.6);
    border-radius: 16px;

    font-weight: 700;
    color: var(--main-text-color);
}

.TextInput__input {
    width: 100%;
    background: linear-gradient(0deg, #E6EEF8, #E6EEF8), #CFD8DC;
}

.TextInput__error {
    border: 1px solid var(--error-text-color);
}

.TextInput__input.TextInput__input-password {
    width: calc(100% - 32px);
}

.TextInput__input:-ms-input-placeholder {
    font-weight: 700;
    color: var(--input-text-color);
}

.TextInput__input::placeholder {
    font-weight: 700;
    color: var(--input-text-color);
}

i {
    position: absolute;
    top: 16px;
    right: 16px;
}

i:hover {
    cursor: pointer;
}
:root {
  --base-background-color: #DCE5F2;
  --active-button-background-color: #9574CD;
  --disabled-button-background-color: #CFD8DC;

  --main-text-color: #546E7A;
  --input-text-color: #90A4AE;
  --active-button-color: #FFFFFF;
  --disabled-button-color: #90A4AE;

  --error-text-color: #F06292;

  --card-shadow-color-1: rgba(253, 255, 255, 0.8);
  --card-shadow-color-2: rgba(187, 195, 206, 0.6);
}

* {
  color: #546E7A;
  color: var(--main-text-color);

  font-family: 'IBM Plex Mono', monospace;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  
  background-color: #DCE5F2;

  
  background-color: var(--base-background-color);
}

.App {
  width: 100vw;
  height: 100vh;
}

input {
  outline: none;
  border: none;
}
